@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

@layer base {
    body {
        @apply font-[Raleway];
    }

    li{
        @apply px-4;
        @apply cursor-pointer;
    
    }

}

video {
    position: relative;
    z-index: -1;
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.navbar {
    position: relative;
    z-index: 10;
}

.Description {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.Description2 {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgb(0, 0, 0);
}

.image-container {
    position: relative;
    display: inline-block;
}

.overlay {
position: absolute;
top: 0;
left: 0;
bottom: 0;
right: 0;
background-color: rgba(35, 64, 77, 0.5);
color: white; 
display: flex;
align-items: center;
justify-content: center;
opacity: 0;
transition: opacity 0.5s;
padding: 10%;
}

.image-container:hover .overlay {
opacity: 1;
}

.video-container {
    position: relative;
    z-index: 10;
    height: 100vh;

}



.ForFun {
    display: flex;
    position: absolute;
    justify-content: center;
}

